.home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home h1{
    background: linear-gradient(to right, #b7f8db, #50a7c2);
    background-clip: text;
    color: transparent;
    transition: transform 0.1s;
    font-size: 40px;
}

.home_form{
    display: flex;
    border: 2px solid rgb(6, 163, 155);
    border-radius: 12px;
    width: 300px;
    height: 40px;
    z-index: 9999;
}

.home_form input{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    height: 38px;
    width: 80%;
    border: 0px;
}
.home_form input:focus{
    outline: none;
}

.home_form input::placeholder {
    font-size: 15px;
    padding: 5px;
}

.home_form button{
    height: 40px;
    width: 19%;
    border: 0px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-size: 17px;
    color: rgb(0, 0, 0);
    font-weight: bold;
}

.buttonSend{
    background: linear-gradient(to right, #b7f8db, #50a7c2);
}

.urlShort{
    margin-top: 25px;
    color: rgb(211, 207, 207);
}

.linkShortUrl{
    text-decoration: none;
    color: #50a7c2;
}